import { Helmet, HelmetProvider } from "react-helmet-async";


const PageHead = () => {
    const pathTitleMap = [
        { path: "/accounts/login", title: "Login | Tagshop" },
        { path: "/accounts/register", title: "Sign up | Tagshop" },
        { path: "/accounts/verify", title: "Verify account | Tagshop" },
        { path: "/accounts/otherinfo", title: "Other Info | Tagshop" },
        { path: "/price", title: "Select plan | Tagshop" },
        { path: "/accounts/forgotpassword", title: "Forgot Password | Tagshop" },
        { path: "/accounts/resetpassword", title: "Reset Password | Tagshop" },
        { path: "/accounts/verifyemail", title: "Verify Email | Tagshop" },
        { path: "/home", title: "Dashboard | Tagshop" },
        { path: "/content/moderation", title: "Moderation | Tagshop" },
        { path: "/content/addfeed", title: "Add Feed | Tagshop" },
        { path: "/content/managefeed", title: "Manage feed | Tagshop" },
        { path: "/content/products", title: "Products | Tagshop" },
        { path: "/more-apps", title: "Apps & More | Tagshop" },
        { path: "/website", title: "Website | Tagshop" },
        { path: "/email-campaign", title: "Email Campaign | Tagshop" },
        { path: "/shopon-bio", title: "Shop On Bio | Tagshop" },
        { path: "/creators", title: "Creator | Tagshop" },
        { path: "/transactions", title: "My Account - Tagshop" },
        { path: "/analytics", title: "Analytics | Tagshop" },
        { path: "/profile", title: "Profile | Tagshop" },
        { path: "/notifications", title: "Notifications | Tagshop" },
        { path: "/onboarding", title: "OnBoarding | Tagshop"},
        { path: "/steps", title: "OnBoarding | Tagshop"}
    ];

    const currentPath = window.location.href;
    const title = pathTitleMap.find(item => currentPath.includes(item.path))?.title || '';

    return <HelmetProvider>
        <Helmet>
            <title>{title}</title>
        </Helmet>
    </HelmetProvider>
}

export default PageHead