import React from 'react'
import { Link } from 'react-router-dom'

export default function FormTerms() {
  return (
    <p className="text-muted text-center fs-9">This site is protected by reCAPTCHA and the Google <Link to="https://policies.google.com/privacy" target="_blank" className="fw-medium  link-active"> Privacy Policy</Link> and <Link to="https://policies.google.com/terms" target="_blank" className="fw-medium  link-active">Terms of Service</Link> apply.</p>
  )
}
