import React, { useEffect } from "react";
import { Loader } from "../CommonComponents/Loader";
import { useLocation, useNavigate } from "react-router-dom";
import { COMPLETE_SHOPIFY_SUBSCRIPTION, GET_PAYMENT_RESPONSE, STRIPE_CHECKOUT } from "../../../../actions/planDetail";
import { useDispatch } from "react-redux";
import { TOAST_DISMISS, TOAST_LOADING } from "../../../../utils";

export default function ShopifyPlanCallback() {

  const navigate = useNavigate();
  const dispatch = useDispatch()
  const location = useLocation();


  useEffect(() => {
    const charge_id = new URLSearchParams(location.search).get("charge_id");
    const channel = new BroadcastChannel('connect');
    if (charge_id) {
      const postbody = {  "session_id": charge_id, type: 'ShopifyPlanCallback' };
      if (window.opener) window.opener.postMessage(JSON.stringify(postbody), '*');
      else {
        if (localStorage.getItem('token')) {
          var obj = { "charge_id": charge_id, "appSubscriptionId": localStorage.getItem('appSubscriptionId') }
          COMPLETE_SHOPIFY_SUBSCRIPTION(obj).then((response) => {
            const { responseData, status } = response.data
            const postbody = { session_id: responseData, type: 'shopifyCallback' };
            if (status) {
              channel.postMessage(JSON.stringify(postbody))
              setTimeout(() => {
                window.close();
              }, 500);
            }
          }).catch((err) => {
            console.log("err", err)
          })
        } else {
          channel.postMessage(JSON.stringify(postbody));
        }
      }
      window.close()
    }

  }, [])


  return (<div className="h-100 w-100 f-center"><Loader /></div>)

}