import { ADD_FEED_ACCOUNT } from "../actions/addFeedActions"
import { GET_LOCAL, TOAST_ERROR, TOAST_SUCCESS } from "."
import { FACEBOOK, GOOGLE_AUTH_TOKEN, GOOGLE_SCOPE, INSTAGRAM, TWITTER_KEY, redirect_uri } from "../actions/socialUrl"

export const addAccountFromId = async (networkId, code, updateAccounts) => {
    const accountFunctions = {
        1: addTwitterAccount,
        2: addInstaPersonal,
        3: AddFbAccount,
        18: AddFbAccount,
        8: addVimeoAccount,
        4: addGoogleAccount
    };

    const selectedFunction = accountFunctions[networkId];
    if (selectedFunction) {
        await selectedFunction(code, updateAccounts);
    }
}

export const addGoogleAccount = async (code, updateAccounts) => {
    try {
        const response = await GOOGLE_AUTH_TOKEN(code);
        const { access_token, expires_in, refresh_token, token_type, id_token } = response.data
        const feedId = GET_LOCAL("feedId");
        const refresh = GET_LOCAL("refresh");
        const accObj = {
            networkId: 4,
            token: access_token,
            expires_in,
            refresh_token,
            scope: GOOGLE_SCOPE,
            token_type,
            id_token,
            created: 1675254640,
            reconnectAccount: refresh ? 1 : 0,
            feedId: feedId ? feedId : 0,
            wallId: localStorage.getItem('wallId')
        };

        const res = await ADD_FEED_ACCOUNT(accObj);
        const { responseCode, responseMessage } = res.data;
        removeAuthLocalData();
        responseCode == 200 ? TOAST_SUCCESS(responseMessage) : TOAST_ERROR(responseMessage);
        updateAccounts();
    } catch (error) {
        console.error("Error adding Google Account:", error);
    }
};

export const AddFbAccount = async (code, updateAccounts) => {
    try {
        const { data } = await FACEBOOK(code);

        if (data) {
            const { access_token, expires_in } = data;
            const feedId = GET_LOCAL("feedId");
            const refresh = GET_LOCAL("refresh");
            const networkId = GET_LOCAL("networkId");

            const accObj = {
                token: access_token || "",
                networkId,
                validity: expires_in || 34656,
                reconnectAccount: refresh ? 1 : 0,
                feedId: feedId ? feedId : 0,
                wallId: localStorage.getItem('wallId')
            };

            const res = await ADD_FEED_ACCOUNT(accObj);
            const { responseCode, resposneMessage } = res.data;

            if (responseCode === 200) {
                TOAST_SUCCESS(resposneMessage);
            } else {
                TOAST_ERROR(resposneMessage);
            }
            removeAuthLocalData();
            updateAccounts();
        }
    } catch (error) {
        console.error("Error adding Account:", error);
    }
}

export const addInstaPersonal = async (code, updateAccounts = () => { }) => {
    const feedId = GET_LOCAL("feedId") || null;
    const refresh = GET_LOCAL("refresh");
    try {
        const res = await INSTAGRAM(code);
        const { access_token } = res.data;
        const accObj = {
            token: access_token || "",
            networkId: 2,
            validity: 34656,
            wallId: localStorage.getItem('wallId'),
            reconnectAccount: refresh ? 1 : 0,
            feedId: feedId ? feedId : 0,
        };

        const { data } = await ADD_FEED_ACCOUNT(accObj);
        const { responseCode, resposneMessage } = data;

        if (responseCode === 200) TOAST_SUCCESS(resposneMessage); else TOAST_ERROR(resposneMessage);

        removeAuthLocalData();
        updateAccounts();
    } catch (error) {
        console.error("Error adding Instagram Account:", error);
    }

}

export const addTwitterAccount = async (code, updateAccounts) => {
    const feedId = GET_LOCAL("feedId");
    const refresh = GET_LOCAL("refresh");
    try {
        const networkId = GET_LOCAL("networkId");
        const accObj = {
            networkId,
            grant_type: "authorization_code",
            code_verifier: "challenge",
            created: 1675254640,
            client_id: TWITTER_KEY,
            redirect_uri,
            code,
            reconnectAccount: refresh ? 1 : 0,
            feedId: feedId ? feedId : 0,
        }
        const { data } = await ADD_FEED_ACCOUNT(accObj);
        const { responseCode, resposneMessage } = data;

        if (responseCode === 200) TOAST_SUCCESS(resposneMessage); else TOAST_ERROR(resposneMessage);
        removeAuthLocalData();
        updateAccounts();
    } catch (error) {
        TOAST_ERROR("Error adding Twitter Account");
        console.error("Error adding Twitter Account:", error);
    }
}

export const addVimeoAccount = async (code, updateAccounts) => {
    const feedId = GET_LOCAL("feedId");
    const refresh = GET_LOCAL("refresh");
    const accObj = {
        token: "w",
        networkId: 8,
        code,
        reconnectAccount: refresh ? 1 : 0,
        feedId: feedId ? feedId : 0,
        wallId: localStorage.getItem('wallId'),
    };

    try {
        const { data } = await ADD_FEED_ACCOUNT(accObj);
        const { responseCode, resposneMessage } = data;

        if (responseCode === 200) TOAST_SUCCESS(resposneMessage); else TOAST_ERROR(resposneMessage);

        removeAuthLocalData();
        updateAccounts();
    } catch (error) {
        // TOAST_ERROR("Error adding Vimeo Account");
        console.error("Error adding Vimeo Account:", error);
    }
}

export const removeAuthLocalData = () => ['refresh', 'feedId', 'connectionType', 'networkId', 'ugc-rights', 'twitterTokenSecret'].forEach((item) => localStorage.removeItem(item))