import React, { useEffect } from "react";
import { Loader } from "../CommonComponents/Loader";
import { useLocation, useNavigate } from "react-router-dom";
import { GET_PAYMENT_RESPONSE, STRIPE_CHECKOUT } from "../../../../actions/planDetail";
import { useDispatch } from "react-redux";
import { TOAST_DISMISS, TOAST_LOADING } from "../../../../utils";

export default function StripeCallback() {

  const navigate = useNavigate();
  const dispatch = useDispatch()
  const location = useLocation();


  useEffect(() => {
    const session_id = new URLSearchParams(location.search).get("session_id");
    const channel = new BroadcastChannel('connect');
    if (session_id) {
      const postbody = { session_id, type: 'stripeCallback' };
      if (window.opener) window.opener.postMessage(JSON.stringify(postbody), '*');
      else {
        if (localStorage.getItem('token')) {
          const toastId = TOAST_LOADING()
          STRIPE_CHECKOUT(session_id).then((response) => {
            const { response_code, responseData } = response.data
            if (response_code == 200) {
              dispatch(GET_PAYMENT_RESPONSE(responseData[0].referenceId, navigate))
            }
            else {
              TOAST_DISMISS(toastId)
              navigate('/price/failed')
            }
          }).catch(err => {
            TOAST_DISMISS(toastId)
            navigate('/price/failed')
            console.log(err)
          })
        } else {
          channel.postMessage(JSON.stringify(postbody));
        }
      }
      window.close()
    }

  }, [])


  return (<div className="h-100 w-100 f-center"><Loader /></div>)

}