import Swal from "sweetalert2"
import { BASE_URL, WASABI_PATH, GET_ACCOUNT_DETAILS } from "../../../../../../actions/api";
import { generateAppSecretProof, getFbCode, getGoogleCode, getTwitterAuthUrl, getVimeoCode } from "../../../../../../utils/auth";
import { FACEBOOK, GET_FB_USER_DATA, GET_GOOGLE_DATA, GET_INSTA_USERDATA, GOOGLE_AUTH_TOKEN, INSTAGRAM } from "../../../../../../actions/socialUrl";
import { removeAuthLocalData } from "../../../../../../utils/addAccount";
import HttpClient from "../../../../../../actions/http-client";
import { baseUrl } from "../../../../../../Assets/AssetsHelper";
import { GET_NETWORK_ICON } from "../../../../../../utils";

export const showAddAccounntAlert = (id) => {
    localStorage.setItem("networkId", id)
    const networkDetails = getNetworkNameById(id);
    const networkName = networkDetails?.networkName || "";
    Swal.fire({
        imageUrl: baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(id)}.svg`),
        imageHeight: 40,
        imageWidth: 40,
        title: `${networkName}`,
        html: `Connect to ${networkName} to pull the content for this feed.`,
        showCloseButton: true,
        showCancelButton: false,
        confirmButtonText: `Continue with ${networkName}`,
        confirmButtonAriaLabel: `Continue with ${networkName}`,
    }).then((result) => {
        if (result.value) {
            const networkId = parseInt(id);
            switch (networkId) {
                case 1:
                    getTwitterAuthUrl();
                    break;
                case 2:
                    const url = `https://api.instagram.com/oauth/authorize?client_id=480810795949098&redirect_uri=${BASE_URL}/feeds/authCallback/&scope=user_profile,user_media&response_type=code`;
                    window.open(url, 'FbAuth', 'width=500,height=600');
                    break;
                case 18:
                    getFbCode(18);
                    break;
                case 3:
                    getFbCode(3);
                    break;
                case 7:
                    getGoogleCode()
                    break;
                case 8:
                    getVimeoCode()
                    break;
                default:
                    break;
            }
        } else if (result.dismiss === "cancel") { }
    });
}

const networksMap = {
    1: { networkName: "Twitter", networkCode: "twitter" },
    2: { networkName: "Instagram", networkCode: "instagram" },
    3: { networkName: "Facebook", networkCode: "facebook" },
    4: { networkName: "Google Reviews", networkCode: "google" },
    5: { networkName: "Pinterest", networkCode: "pinterest" },
    6: { networkName: "Flickr", networkCode: "flickr" },
    7: { networkName: "YouTube", networkCode: "youtube" },
    8: { networkName: "Vimeo", networkCode: "vimeo" },
    18: { networkName: "Instagram", networkCode: "instagram-business" },
    23: { networkName: "Airbnb Reviews", networkCode: "airbnb" },
    28: { networkName: "TikTok", networkCode: "tiktok" },
    29: { networkName: "SnapUp", networkCode: "onsiteupload" },
    33: { networkName: "Collab with Creators", networkCode: "collab-with-creators" }
};

export const getNetworkNameById = (id) => networksMap[id];

export const addInstaPersonalAccount = async (code, callback) => {
    try {
        const res = await INSTAGRAM(code);
        const { access_token } = res.data;

        const data = await GET_INSTA_USERDATA(access_token);
        removeAuthLocalData();
        if (data) callback(data);
    } catch (error) {
        callback(null);
        console.error("Error adding Instagram Account:", error);
    }
}

export const addFacebookAccount = async (code, callback) => {
    try {
        const { data } = await FACEBOOK(code);
        const appSecretProof = generateAppSecretProof(data.access_token)
        const res = await GET_FB_USER_DATA(data.access_token, appSecretProof);
        removeAuthLocalData();
        if (res) callback(res);
    } catch (error) {
        callback(null);
        console.error("Error during Facebook authentication:", error);
    }
}

export const googleAccDetails = async (code, callback) => {
    try {
        const { data: { access_token } } = await GOOGLE_AUTH_TOKEN(code);
        const { data } = await GET_GOOGLE_DATA(access_token);
        removeAuthLocalData();
        var picture = data.picture ? data.picture : "";
        const obj = { accountFullName: data.name, accountEmailId: data.email, createFrom: 'google', picture, authId: data.sub }
        if (obj) callback(obj);
    } catch (error) {
        callback(null);
        console.error("Error during Google authentication:", error);
    }
}

export const getAccountDetails = (code, networkId, callback) => {
    new HttpClient().post(GET_ACCOUNT_DETAILS, { token: code, code, networkId }).then(response => {
        const { responseCode, responseData } = response.data;
        if (responseCode == 200) callback(responseData);
    }).catch(ex => {
        callback(null);
        console.error("Error during getAccountDetails authentication:", ex);
    })
}