import { ProgressBar, Row, Col } from "react-bootstrap";
import { generateRandomArray } from "../../../../utils";

const PwdStrength = ({ strength }) => {
    return <div className="w-100  d-flex align-items-end flex-column mb-2" style={{ minHeight: 33 }}>
        <Row className="gx-1 w-100 mb-1">
            {generateRandomArray(4).map((itm, index) => {
                const currentStrength = index + 1;
                const progress = strength >= currentStrength ? 100 : 0;
                let variant;

                if (strength === 4) variant = 'success';
                else if (strength === 3 && currentStrength <= 3) variant = 'primary';
                else if (strength === 2 && currentStrength <= 2) variant = 'warning';
                else {
                    if (currentStrength == 4) variant = 'success';
                    else if (currentStrength == 3) variant = 'primary';
                    else if (currentStrength == 2) variant = 'warning';
                    else variant = 'danger'
                }

                return (
                    <Col key={currentStrength}>
                        <ProgressBar now={progress} variant={variant} className='h-2px' />
                    </Col>
                );
            })}
        </Row>
        <span className="fs-8 text-muted">{strength == 4 ? 'Strong' : strength == 3 ? 'Good' : strength == 2 ? 'Weak' : 'Too Short'}</span>
    </div>
}
export default PwdStrength
