import { baseUrl } from "../../../../Assets/AssetsHelper"
export const Loader = ({ bg }) => {
       return <div className={`loader__ position-absolute h-100 w-100 f-center top-0 start-0 bg-${bg} rounded-3`}>
              <img src={baseUrl('media/images/spinner.gif')} height={70} width={70} alt="Tagshop Spinner" className="img-fluid" />
       </div>
}