import React, { useEffect } from 'react'
import { useNavigate, useSearchParams } from 'react-router-dom'
import { Loader } from '../app/Layout/Main/CommonComponents/Loader';
import { GET_LOCAL } from '.';
import { addAccountFromId } from './addAccount';

const sendCode = (code, navigate) => {
  const channel = new BroadcastChannel('connect');
  const networkId = GET_LOCAL('networkId');
  const type = localStorage.getItem('connectionType');
  const pathname = localStorage.getItem('currRedirectUrl');

  if (code) {
    const postbody = { error: false, code: code, networkId, type };
    if (networkId == 1 && typeof code == "string") {
      addAccountFromId(1, code, () => {
        localStorage.removeItem('currRedirectUrl');
        navigate(pathname);
      });
      return;
    }
    if (window.opener) window.opener.postMessage(JSON.stringify(postbody), '*');
    else channel.postMessage(JSON.stringify(postbody));
    window.close()
  }
}

const Authorization = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const isUgcRequest = localStorage.getItem('ugc-rights');
  let code = searchParams.get("code");
  if (isUgcRequest) {
    code = {
      oauth_token: searchParams.get("oauth_token"),
      oauth_verifier: searchParams.get("oauth_verifier"),
      tokenSecret: localStorage.getItem('twitterTokenSecret')
    }
  }

  useEffect(() => {
    sendCode(code, navigate);
  }, [searchParams])

  return (
    <div className='h-100vh w-100 f-center'><Loader /></div>
  )
}

export default Authorization