import React, { Component } from 'react';
import { Collapse } from 'react-bootstrap';
import ReactHtmlParser from 'react-html-parser';

class ShowMoreContent extends Component {
       state = { readMore: false, lines: 0 }

       readMoreTxt = () => {
              this.setState({ readMore: !this.state.readMore })
       }

       countLines = (id) => {
              setTimeout(() => {
                     var el = document.getElementById(id);
                     var divHeight = el ? el.offsetHeight : 40
                     var lines = divHeight / 22;
                     this.setState({ lines: lines });
              }, 100);
       }
       render() {
              const { content, idIndex, line, classes } = this.props;
              const { readMore, lines } = this.state;

              if (content && content.length > 0) {
                     this.countLines(`constent-${idIndex}`)
              }

              return <div className={classes}>
                     <Collapse in={readMore} className={`d-block pb-0 mb-0 overflow-hidden position-relative ${lines > line ? !readMore ? 'show_more_content' : '' : ''}`}>
                            <div className="w-100">
                                   <p id={`constent-${idIndex}`} className="pb-1 mb-0 text-break">{ReactHtmlParser(content)}</p>
                            </div>
                     </Collapse>
                     {lines > line ?
                            <button type="button" className="btn btn-sm btn-link text-decoration-underline fw-semibold p-0" onClick={()=>this.readMoreTxt()}>
                                   {readMore ? 'Show Less' : 'Show More'}
                            </button>
                            : null}
              </div>
       }
}

export default ShowMoreContent;