import axios from 'axios';
import HttpClient from "./http-client";
import { CHECK_STRIPE_SESSION, ENABLE_EC_FREE_PLAN, PRODUCT_PLAN_DETAIL, PRODUCT_PLAN_UPDATE, SHOPIFY_GET_SUBSCRIPTIONS_LINK, SHOPIFY_SUBSCRIPTIONS_COMPLETED, SUCCESS_STRIPE_CALL } from './api'
import { DISPATCH_STRIPE_CALL, PLAN_PRICES_DETAILS, PLAN_PRICES_UPDATE } from '../reducer/keys';
import { TOAST_DISMISS } from '../utils';




export const PLAN__DETAILS = () => { 
    return (dispatch) => {
        new HttpClient().get(PRODUCT_PLAN_DETAIL).then((response) => {
            const { responseData } = response.data
            dispatch({ type: PLAN_PRICES_DETAILS, payload: responseData });
        })
    }
}


export const UPDATE_PLAN__DETAILS = (planId) => { 
   return  new HttpClient().post(PRODUCT_PLAN_UPDATE, {"plan_id":planId})
    
}


export const UPDATE_EC_FREE_PLAN = (planId) => {  
  return  new HttpClient().post(ENABLE_EC_FREE_PLAN, {"plan_id":planId})
}



export const GET_SHOPIFY_SUBSCRIPTION_LINK = (obj) => { return  new HttpClient().post(SHOPIFY_GET_SUBSCRIPTIONS_LINK,obj) }
export const COMPLETE_SHOPIFY_SUBSCRIPTION = (obj) => { return  new HttpClient().post(SHOPIFY_SUBSCRIPTIONS_COMPLETED,obj) }

export const STRIPE_CHECKOUT = (id) => { 
  return new HttpClient().post(CHECK_STRIPE_SESSION, {"sessionId":id})
}
 

 export const GET_PAYMENT_RESPONSE = (id, history) => {
    var data = {"referenceId":id, "adminId":0}
    return (dispatch) => {
        new HttpClient().post(SUCCESS_STRIPE_CALL, data).then((response) => {
            const { responseCode, responseData } = response.data
            TOAST_DISMISS()
            if(responseCode == 200){
                dispatch({ type: DISPATCH_STRIPE_CALL, payload: responseData });
                history('/price/success')
              }
              else{
                 history('/price/failed')
              }
            }).catch((err)=>{
              TOAST_DISMISS()
              history('/price/failed')
            })
    }
 }