import axios from "axios"
import { UGC_CERTIFICATE_DOWNLOAD, UGC_CHECK_ACCOUNT, UGC_POST_ACTION, UGC_POST_DETAIL } from "./api"

export const GET_UGC_POST_DETAIL_FROM_ID = (id) => {
    return axios.get(`${UGC_POST_DETAIL}/${id}`)
}

export const APPROVE_REJECT_ACTION = (obj) => {
    return axios.post(UGC_POST_ACTION, obj)
}

export const DOWNLOAD_CERTIFICATE = (id) => {
    axios.get(`${UGC_CERTIFICATE_DOWNLOAD}/${id}`, {headers: {authorization: `Bearer ${localStorage.getItem('token')}`}, responseType: 'arraybuffer'},).then(function (response) {
        const a = document.createElement('a');
        a.href = window.URL.createObjectURL(new Blob([response.data]));
        a.download = `${id}.pdf`;
        document.body.appendChild(a);
        a.click();
        a.remove();
    });
}

export const CHECK_USER_ACCOUNT = (obj) => {
    return axios.post(UGC_CHECK_ACCOUNT,obj)
}